import React, { Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import axios from 'axios';
import _ from 'lodash';
import Panel from 'react-bootstrap/lib/Panel';
import { Button, Modal, FormControl, Form, FormGroup, Col, ControlLabel } from 'react-bootstrap';
import { connect } from 'react-redux';
import Select from 'react-select';

import Pagination from 'react-bootstrap/lib/Pagination';
import { receiveProduct } from '../../../actions/fetchData';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showForm: false,
      name: '',
      size: '',
      code: '',
      quantity: '',
      type: '',
      id: '',
      instock: 1,
    };
  }
  componentDidMount() {
    const { loaded } = this.props;
    if (!loaded) {
      axios.get('/auth/product', ).then(
        (res) => {
          this.props.dispatch(receiveProduct(res.data));
        }
      )
    }
  }
  open(type, id) {
    this.setState({
      showForm: true,
      type,
    });
    if ( type === 'edit') {
      const product = this.props.products.find(product => product.id === id);
      this.setState({
        name: product.name,
        code: product.code,
        quantity: product.quantity,
        size: product.size,
        id: product.id,
        instock: product.instock,
      });
    } else {
      this.setState({
        name: '',
        code: '',
        quantity: 0,
        size: '',
        id: '',
        instock: 1,
      });
    }
  }
  close() {
    this.setState({
      showForm: false,
    });
  }
  onChange(type, e) {
    const value = e.target.value;
    if (value.length > 180) return;
    this.setState({
      [type]: value,
    });
  }
  replaceProduct(products, target) {
    let clone = [...products];
    clone = clone.map((product) => {
      if (product.id === target.id) {
        return target;
      }
      return product;
    });
    return clone;
  }
  addProduct(e) {
    e.preventDefault();
    const { name, size, code, quantity, type, id, instock } = this.state;
    if (name.length < 1) return;
    if (type === 'edit') {
      axios.put('/auth/product', {
        name,
        size,
        code,
        quantity: quantity || 0,
        id,
        instock,
      })
        .then(
          (res) => {
            this.close();
            this.props.dispatch(receiveProduct(this.replaceProduct(this.props.products, res.data)));
          },
          (err) => {
            alert('C li xy ra hoc ti khon  c s dng');
          }
        )
    } else {
      axios.post('/auth/product', {
        name,
        size,
        code,
        quantity: quantity || 0,
        instock,
      })
        .then(
          (res) => {
            this.close();
            this.props.dispatch(receiveProduct([...this.props.products, res.data]));
          },
          (err) => {
            alert('C li xy ra hoc ti khon  c s dng');
          }
        )
    }
  }
  removeProduct(id) {
    if (confirm('Bn c chc chn ?')) {
      axios.delete(`/auth/product/${id}`)
      .then(
        (res) => {
           this.props.dispatch(receiveProduct(this.props.products.filter(product => product.id != id)));
        },
        (err) => {
          alert('Co loi xay ra');
        }
      )
    }
  }
  logChange(v) {
    this.setState({ instock: v.value });
  }
  render() {
    const { products, user } = this.props;
    const { showForm, name, username, password, role, type, size, code, quantity, instock } = this.state;
    var options = [
  { value: 1 , label: 'Cn hng' },
  { value: 0, label: 'Ht hng' }
];
    return (
      <div className="row ng-scope">
        <div className="">
        <p></p>
        <div className="text-right">
        {
          user.role < 3 ?
          <Button bsStyle="success" active onClick={this.open.bind(this, 'add')}>
            Thm sn phm
          </Button>
          : null
        }
        </div>
        <p></p>
          <Panel header={<span>Danh sch sn phm </span>} >
            <div className="table-responsive">
              <table className="table table-striped table-bordered table-hover">
                <thead>
                  <tr>
                    <th># </th>
                    <th>Tn </th>
                    <th>M</th>
                    <th>Size</th>
                    <th>S lng </th>
                    <th>Trng thi</th>
                    {
                      user.role < 3 ?
                      <th>Thao tc</th>
                      : null
                    }
                  </tr>
                </thead>
                <tbody>
                  {
                    products.map((product, index) => {
                      return (
                        <tr key={product.id}>
                          <td>{index} </td>
                          <td>{product.name} </td>
                          <td>{product.code} </td>
                          <td>{product.size} </td>
                          <td>{product.quantity} </td>
                          <td>
                            {
                              product.instock == 1 ?
                                <Button bsStyle="success" bsSize="xs"> Cn hng</Button>
                                :
                                <Button bsStyle="danger" bsSize="xs"> Ht hng</Button>
                            }
                          </td>
                          {
                            user.role < 3 ?
                            <td>
                              <Button bsStyle="danger" bsSize="xs" active onClick={this.removeProduct.bind(this, product.id)}>
                                Xa
                              </Button>
                              &nbsp;
                              <Button bsStyle="info" bsSize="xs" active onClick={this.open.bind(this, 'edit', product.id)}>
                                Chnh sa
                              </Button>
                            </td>
                            : null
                          }
                        </tr>
                      );
                    })
                  }
                </tbody>
              </table>
            </div>
          </Panel>
          <Modal show={showForm} onHide={this.close.bind(this)}>
          <Modal.Header closeButton>
            <Modal.Title>{type === 'add' ? 'Thm sn phm' : 'Chnh sa'}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
          <Form horizontal>
           <FormGroup>
             <Col componentClass={ControlLabel} sm={2}>
               Tn
             </Col>
             <Col sm={10}>
               <FormControl
               type="text"
               onChange={this.onChange.bind(this, 'name')}
               value={name}
               />
             </Col>
           </FormGroup>

           <FormGroup >
             <Col componentClass={ControlLabel} sm={2}>
               M sn phm
             </Col>
             <Col sm={10}>
               <FormControl
               type="text"
               onChange={this.onChange.bind(this, 'code')}
               value={code}
               />
             </Col>
           </FormGroup>
           <FormGroup >
             <Col componentClass={ControlLabel} sm={2}>
               Size
             </Col>
             <Col sm={10}>
               <FormControl
               type="text"
               onChange={this.onChange.bind(this, 'size')}
               value={size}
               />
             </Col>
           </FormGroup>
           <FormGroup >
             <Col componentClass={ControlLabel} sm={2}>
               S lng
             </Col>
             <Col sm={10}>
               <FormControl
               type="number"
               onChange={this.onChange.bind(this, 'quantity')}
               value={quantity}
               />
             </Col>
           </FormGroup>
           <FormGroup >
             <Col componentClass={ControlLabel} sm={2}>
              Trng thi
             </Col>
             <Col sm={10}>
               <Select
               name="form-field-name"
               value={instock}
               placeholder="Trng thi"
               options={options}
               onChange={this.logChange.bind(this)}
               clearable={false}
               />
             </Col>
           </FormGroup>
           <FormGroup>
             <Col smOffset={2} sm={10}>
               <Button onClick={this.addProduct.bind(this)} bsStyle="success" bsSize="large">
                 {type === 'add' ? 'Thm sn phm' : 'Chnh sa'}
               </Button>
             </Col>
           </FormGroup>
          </Form>
          </Modal.Body>
        </Modal>
        </div>
      </div>
    );
  }
}
export default connect((state) => {
  return {
    loaded: state.data.product.loaded,
    products: state.data.product.data,
    user: state.data.user,
  };
})(Home);



