import React, { Component } from 'react';
import classNames from 'classnames';
import { connect } from 'react-redux';
import history from '../../core/history';

class Sidebar extends Component {

  constructor(props) {
    super(props);
    this.state = {
      uiElementsCollapsed: true,
      chartsElementsCollapsed: true,
      multiLevelDropdownCollapsed: true,
      thirdLevelDropdownCollapsed: true,
      samplePagesCollapsed: true,
    };
  }

  render() {
    const { user } = this.props;
    return (
      <div className="navbar-default sidebar" style={{ marginLeft: '-20px' }} role="navigation">
        <div className="sidebar-nav navbar-collapse collapse">
          <ul className="nav in" id="side-menu">
            {
              user.role == 1 ?
              <li>
                <a href="" onClick={(e) => { e.preventDefault(); history.push('/users'); }} >
                  <i className="fa fa-users" /> &nbsp;Qun l nhn vin
                </a>
              </li>
              :
              null
            }
            <li>
              <a href="" onClick={(e) => { e.preventDefault(); history.push('/product'); }} >
                <i className="fa fa-table fa-fw" /> &nbsp;Kho hng
              </a>
            </li>

            <li>
              <a href="" onClick={(e) => { e.preventDefault(); history.push('/bill'); }} >
                <i className="fa fa-sticky-note-o" /> &nbsp;n hng
              </a>
            </li>

            <li>
              <a href="" onClick={(e) => { e.preventDefault(); history.push('/forms'); }} >
                <i className="fa fa-address-book-o" /> &nbsp;Khch hng
              </a>
            </li>
            <li>
              <a href="" onClick={(e) => { e.preventDefault(); history.push('/forms'); }} >
                <i className="fa fa-bar-chart" /> &nbsp;Thng k
              </a>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}


export default connect((state) => {
  return {
    user: state.data.user,
  };
})(Sidebar);



